import React,{Component} from 'react'
import { Row, Col } from 'antd';

class Info extends React.Component {
    constructor(props) {
        super(props);
    }

    render() {
        return (
            <Row className="activityInfo">
                {this.props.children}
            </Row>
        )
    }
}

module.exports = Info;



